<template>
  <v-container>
    <v-card class="mx-auto mt-5 text-center">
      <v-card-title>
        <h1 class="display-1 mx-auto">Dashboard</h1>
      </v-card-title>
      <v-card-text class="purple--text subtitle">
        <p>
          Welcome to your dashboard
          <strong class="text-uppercase success--text">{{ username }}</strong>
        </p>
        <p>
          <v-btn
            color="purple"
            :to="{ name: 'invoices' }"
            text
            elevation="2"
            rounded
            x-large
            >invoices</v-btn
          >
        </p>
      </v-card-text>
      <v-divider></v-divider>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="info" @click="logout">Logout</v-btn>
      </v-card-actions>
    </v-card>
  </v-container>
</template>

<script>
import store from "@/store.js";
export default {
  name: "Dashboard",

  data: () => ({
    username: store.user.username
  }),
  methods: {
    logout() {
      store.user = null;
      this.$router.push("/");
    }
  }
};
</script>
